import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)


const router = new Router({
	routes: [
		{
			path: '',
			redirect: 'luckDraw'
		},
		{
			path: '/home/:id?',
			name: 'home',
			component: () => import('./views/Home.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/rule',
			name: 'rule',
			component: () => import('./views/Rule.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/luckDraw/:id?',
			name: 'luckDraw',
			component: () => import('./views/LuckDraw.vue'),
			meta: {
				keepAlive: true
			}
		},
		{
			path: '/luckDrawRule',
			name: 'luckDrawRule',
			component: () => import('./views/LuckDrawRule.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/agreement',
			name: 'agreement',
			component: () => import('./views/Agreement.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/helpcenter',
			name: 'helpcenter',
			component: () => import('./views/HelpCenter.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/about',
			name: 'about',
			component: () => import('./views/About.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/list/:id?',
			name: 'list',
			component: () => import('./views/List.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/numberGuess/:id?',
			name: 'numberGuess',
			component: () => import('./views/NumberGuess.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/numberGuessRule',
			name: 'numberGuessRule',
			component: () => import('./views/NumberGuessRule.vue'),
			meta: {
				keepAlive: false
			}
		},
		{
			path: '/numberGuessList',
			name: 'numberGuessList',
			component: () => import('./views/NumberGuessList.vue'),
			meta: {
				keepAlive: false
			}
		},
	]
})


router.afterEach((to, from, next) => {

	window.scrollTo(0, 0);

});

export default router;
